import React, { useState, useEffect } from 'react';
import config from './config';
import Highcharts from 'highcharts';
import HighchartsMore from 'highcharts/highcharts-more/';
const ReactHighcharts = require('react-highcharts').withHighcharts(Highcharts);
HighchartsMore(ReactHighcharts.Highcharts);


export default function BubbleChart(props) {

    let { data, title } = props;
    console.log(data);
    const options = {
        chart: {
            type: 'packedbubble',
            height: '60%'
        },
        credits: { enabled: false },

        title: {
            text: title
        },
        tooltip: {
            useHTML: true,
            pointFormat: '<b>{point.name}:{point.y}</b>'
        },
        plotOptions: {
            packedbubble: {
                minSize: '50%',
                maxSize: '160%',
                zMin: 0,
                zMax: 3000,
                layoutAlgorithm: {
                    splitSeries: false,
                    gravitationalConstant: 0.02
                },
                dataLabels: {
                    enabled: true,
                    format: '{point.name}',
                  /*   filter: {
                        property: 'y',
                        operator: '>',
                        value: 250
                    }, */
                    style: {
                        color: 'black',
                        textOutline: 'none',
                        fontWeight: 'normal'
                    }
                }
            }
        },
        series: data
    }
    return (
        <div  >
            <ReactHighcharts
                config={options}
            />
        </div>
    )
}
